<template>
  <div id="app">
    <div class="container-fluid">
        <div class="row">
            <jain-sidenav ref="sidenav">
                <div class="logo">
                    <a href="/" @click="closeSidenav" title="Home" rel="noopener">
                        <img src="static/images/logo.svg" alt="Logo">
                    </a>
                </div>
                <jain-profile-picture image="./static/images/profile.jpg">
                    <p slot="name">Pardeep Jain</p>
                    <p slot="position">Sr. Software Engineer, Wildnet Technologies</p>
                </jain-profile-picture>
                <jain-drawer-list ref="drawerList">
                    <li>
                        <router-link to="/profile" :class="{'router-link-active': $route.fullPath ==='/' || $route.fullPath === '/profile'}">
                            <span @click="closeSidenav">
                                <svg viewBox="0 0 24 24">
                                    <path d="M12,4A4,4 0 0,1 16,8A4,4 0 0,1 12,12A4,4 0 0,1 8,8A4,4 0 0,1 12,4M12,14C16.42,14 20,15.79 20,18V20H4V18C4,15.79 7.58,14 12,14Z" />
                                </svg>
                                Profile
                            </span>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/resume">
                            <span @click="closeSidenav">
                                <svg viewBox="0 0 24 24">
                                    <path d="M13,9H18.5L13,3.5V9M6,2H14L20,8V20A2,2 0 0,1 18,22H6C4.89,22 4,21.1 4,20V4C4,2.89 4.89,2 6,2M15,18V16H6V18H15M18,14V12H6V14H18Z" />
                                </svg>
                                Resume
                            </span>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/projects">
                            <span @click="closeSidenav">
                                <svg viewBox="0 0 24 24">
                                    <path d="M3,3H9V7H3V3M15,10H21V14H15V10M15,17H21V21H15V17M13,13H7V18H13V20H7L5,20V9H7V11H13V13Z" />
                                </svg>
                                Projects
                            </span>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/contact">
                            <span @click="closeSidenav">
                                <svg viewBox="0 0 24 24">
                                    <path d="M13,9H18.5L13,3.5V9M6,2H14L20,8V20A2,2 0 0,1 18,22H6C4.89,22 4,21.1 4,20V4C4,2.89 4.89,2 6,2M15,18V16H6V18H15M18,14V12H6V14H18Z" />
                                </svg>
                                Contact Me
                            </span>
                        </router-link>
                    </li>
                </jain-drawer-list>
                <div class="made-with">
                    <p>Made with <img src="static/images/heart.svg" alt=""> and <a href="https://vuejs.org" title="Vue.js" rel="noopener"><img src="static/images/vue-logo.svg" alt=""></a></p>
                </div>
            </jain-sidenav>
            <div class="app-layout">
                <div class="header">
                    <jain-toolbar fixed>
                        <button class="nav-toggle" @click="toggleSidenav" aria-label="Toggle navigation">
                            <svg viewBox="0 0 24 24">
                                <path d="M3,6H21V8H3V6M3,11H21V13H3V11M3,16H21V18H3V16Z" />
                            </svg>
                        </button>
                        <div class="page-title"><h1>{{ this.$route.name }}</h1></div>
                        <version-badge version="v4.2-vue"></version-badge>
                    </jain-toolbar>
                </div>

                <main>
                    <router-view></router-view>
                </main>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  methods: {
        toggleSidenav() {
            this.$refs.sidenav.toggle();
        },

        closeSidenav() {
            this.$refs.sidenav.close();
        }
    }
}
</script>
<style>
    @import '/static/css/app.css'
</style>



