// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import LazyImg from 'v-lazy-img';
import Router from 'vue-router'
import VueMasonryPlugin from 'vue-masonry';

import App from './App'

// DIRECTIVES
Vue.directive('ripple', require('./directives/Ripple.js'));

import JainSidenav from './components/jain-sidenav/jain-sidenav.vue';
import JainProfilePicture from './components/jain-profile-picture/jain-profile-picture.vue';
import JainDrawerList from './components/jain-drawer-list/jain-drawer-list.vue';
import JainToolbar from './components/jain-toolbar/jain-toolbar.vue';
import JainProject from './components/jain-project/jain-project.vue';
import JainTimeline from './components/jain-timeline/jain-timeline.vue';
import TimelineItem from './components/jain-timeline/jain-timeline-item.vue';
import JainProfile from './components/jain-profile/jain-profile.vue';
import JainProfileItem from './components/jain-profile/jain-profile-item.vue';
import JainProgressbar from './components/jain-progressbar/jain-progressbar.vue';
import NanoRipple from './components/nano-ripple/nano-ripple.vue';
import VersionBadge from './components/version-badge/version-badge.vue';

// VUE COMPONENTS
Vue.component('jain-sidenav', JainSidenav);
Vue.component('jain-profile-picture', JainProfilePicture);
Vue.component('jain-drawer-list', JainDrawerList);
Vue.component('jain-toolbar', JainToolbar);
Vue.component('jain-project', JainProject);
Vue.component('jain-timeline', JainTimeline);
Vue.component('timeline-item', TimelineItem);
Vue.component('jain-profile', JainProfile);
Vue.component('profile-item', JainProfileItem);
Vue.component('jain-progressbar', JainProgressbar);
//Vue.component('jain-card-cover', require('./components/jain-card/jain-card-cover.vue'));
//Vue.component('jain-card-reveal', require('./components/jain-card/jain-card-reveal.vue'));

Vue.component('nano-ripple', NanoRipple);
//Vue.component('nano-tabs', require('./components/nano-tabs/nano-tabs.vue'));
//Vue.component('nano-tab', require('./components/nano-tabs/nano-tab.vue'));

Vue.component('version-badge', VersionBadge);

// V-LAZY-IMG
Vue.use(LazyImg);

// VUE-MASONRY
Vue.use(VueMasonryPlugin);

Vue.config.productionTip = false

Vue.use(Router)

// IMPORT FOR FIRST/MOST IMPORTANT COMPONENT, CONST FOR ASYNC LOADING
import Profile from './views/profile.vue';
import Contact from './views/contact.vue';
import Resume from './views/resume.vue';
import Projects from './views/projects.vue';
const notfound = require('./views/notfound.vue');


const router = new Router({
  routes: [
  	{ path: '/', component: app, name: 'App ' },
    { path: '/resume', component: Resume, name: 'Resume' },
    { path: '/contact', component: Contact, name: 'Contact' },
    { path: '/profile', component: Profile, name: 'Profile' },
    { path: '/projects', component: Projects, name: 'Projects' },
    { path: '/404', component: notfound, name: '404 - not found' },
    { path: '*', redirect: '404' }
  ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})


